// input style
@import "../../base/variables/options";
@import "../../animation/variable";
@import "../../base/variables/form";
@import "../../base/mixins/box-shadow";
@import "../../ripple/mixin";
@import "../../theme/mixin";
@import "../../theme/variable";
@import "../../shadow/mixin";
@import "variable";
@import "function";
@import "mixin";

//  Input 
.input {
  position:   relative;
  box-sizing: border-box;
  display:    inline-block;
  width:      $form-input-width;

  //  label 
  label > input,
  label + input {
    margin-left: $input-margin-left;
  }
}

//  Input 
.input-field {
  box-sizing:              border-box;
  width:                   100%;
  padding:                 $input-padding-y $input-padding-x;
  border:                  1px solid $input-border-color;
  line-height:             $input-line-height;
  background:              none;
  background-clip:         padding-box;
  will-change:             opacity, transform, color;
  -webkit-appearance:      none; //  input 
  -webkit-font-smoothing:  antialiased; // 
  -moz-osx-font-smoothing: grayscale; // 

  @if $enable-rounded {
    border-radius: $input-border-radius;
  } @else {
    border-radius: 0;
  }

  // Placeholder
  &::placeholder {
    @include theme-prop(color, hint-on-light);
    opacity:    1;
    transition: input-transition(opacity);
  }

  //  `:focus` 
  &:focus {
    border-color: $grey-color-500;
    outline:      none;
    transition:   input-transition(opacity);

    //  placeholder
    &::placeholder {
      opacity: 0;
    }
  }

  // Disabled
  &:disabled,
  &[readonly] {
    @include theme-prop(color, disabled-on-light);
    pointer-events: none;
  }

  //  type="color" 
  &[type="color"] {
    //  +  + 
    min-height: $font-size-base * $input-line-height + $input-padding-y * 2 + .125rem;
    padding: 0;
    border: 0;
  }
}

//  .input placeholder 
//  input placeholder 
//  .input-placeholder
.input-placeholder {
  .input-field {
    &::placeholder {
      transition: input-transition(color);
    }
    &:focus {
      &::placeholder {
        opacity: 1;
        @include theme-prop(color, secondary-on-light);
      }
    }
  }
}

// 
.input-invalid {
  .input-field {
    @include theme-assist(border-color, error);

    // Placeholder
    &::placeholder {
      @include theme-assist(color, error);
    }
  }
}

// 
.input-flat {
  padding-left:  0;
  padding-right: 0;
  border-width:  0 0 1px;
  border-radius: 0;
}

// full width input
.input-full {
  display: block;
  flex:    1;
  width:   100%;

  .input-field {
    display: block;
  }
}

.input-label {
  position:         absolute;
  bottom:           $input-label-bottom;
  left:             0;
  pointer-events:   none;
  cursor:           text;
  transform-origin: left top;
  transition:       input-transition(transform), input-transition(color), input-transition(font-size);
  @include theme-prop(color, hint-on-light);
}

// input
.input-float-above {
  margin-top: 1.5rem;

  .input-field {
    padding-top:   0;
    padding-left:  0;
    padding-right: 0;
    border-width:  0 0 1px;
    border-radius: 0;
    &::placeholder {
      opacity: 0;
    }
  }

  //  input label 
  // .input-field:-webkit-autofill + .input-label
  .input-field:focus + .input-label,
  .input-field-focused + .input-label,
  .input-field:-webkit-autofill + .input-label {
    cursor:    auto;
    transform: translateY(-90%) scale(.8, .8);
    @include theme-prop(color, primary-on-light);
  }
}

// 
.input-invalid {
  .input-label,
  .input-field:focus + .input-label,
  .input-field-focused + .input-label,
  .input-field:-webkit-autofill + .input-label {
    @include theme-assist(color, error);
  }
}

// 
.input-icon {
  position: absolute;
  top:      .5rem;
  cursor:   pointer;
}

.input-icon-start {
  .input-field {
    padding-left: 2.5rem;
  }

  .input-icon {
    left:  .5rem;
    right: initial;
  }

  .input-label {
    left: 2.5rem;
  }
}

.input-icon-end {
  .input-field {
    padding-right: 2.5rem;
  }

  .input-icon {
    left:  initial;
    right: .5rem;
  }

  .input-label {
    right: 2.5rem;
  }
}

.input-float-above {
  .input-icon {
    top: -0.5rem;
  }
}

// Sizing
.input-lg {
  @include input-size($input-font-size-lg, $input-padding-x-lg, $input-padding-y-lg, $input-icon-top-lg, 1.25, 0);
}

.input-sm {
  @include input-size($input-font-size-sm, $input-padding-x-sm, $input-padding-y-sm, $input-icon-top-sm, .75, -.5rem);
}

// 
@if ($enable-custom-theme and $enable-custom-theme-for-input) {
  // 
  @each $style in ("primary", "secondary") {
    .input-#{$style} {
      .input-field {
        @include theme-prop(border-color, #{$style}-lighter);

        &:focus {
          @include theme-prop(border-color, #{$style});
        }
      }
    }
  }

  //  input
  @each $theme in $theme-assist {
    $color: map-get($theme-assist-colors, $theme);
    .input-#{$theme} {
      .input-field {
        @include theme-assist(border-color, #{$theme}-light);

        &:focus {
          @include theme-assist(border-color, #{$theme});
        }
      }
    }
  }
}



